.header-container {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  color: #000;
  z-index: 1000;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  background-color: #fff;
  color: #000;
  padding: 0 20px;
  border-bottom: 1px solid #ccc;
  height: 60px;
}

.contact-icon1 {
  font-size: 1.5rem;
  color: #007bff;
  margin: 0 5px;
  transition: color 0.3s;
}
.contact-icon1:hover {
  transform: translateX(-5px);
}

.contact-icon2 {
  font-size: 1.5rem;
  color: #3f3d3d;
  margin: 0 5px;
  transition: color 0.3s;
}
.contact-icon2:hover {
  transform: translateX(-5px);
}

.contact-icon3 {
  font-size: 1.5rem;
  color: rgb(187, 39, 39);
  margin: 0 5px;
  transition: color 0.3s;
}
.contact-icon3:hover {
  transform: translateX(-5px);
}

.sections-container {
  display: flex;
  flex-grow: 1;
  align-items: center;
  font-size: 10px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  transition: color 0.3s;
  text-transform: uppercase;
  padding: 0 20px;
  margin: 0 10px;
  font-family: helvetica;
  margin-left: auto;
  margin-bottom: 70px;
}

.contact-text-header {
  color: #4b4949;
  text-decoration: none;
  font-size: 14px;
  margin-right: 20px;
  margin-top: 50px;
  gap: 3px;
}

.contact-text-header:hover {
  text-decoration: underline;
  color: #333;
}

.name-title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  animation: slide-in 5s;
  color: #4b4949;
  padding: 15px;
}

.name {
  color: #4b4949;
  font-size: 1.2rem;
  font-family: Helvetica;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.title {
  font-size: 1rem;
  font-family: Helvetica;
}

@keyframes slide-in {
  from {
    transform: translateX(-300%);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}
