body {
  background-image: linear-gradient(
      rgba(255, 255, 255, 0.322),
      rgba(255, 255, 255, 0.84)
    ),
    url('https://images.unsplash.com/photo-1560780552-ba54683cb263?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1740&q=80');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.content-container {
  position: relative;
  z-index: 1;
}
.about-contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}
