.about-me {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-bottom: 3px;
  margin-top: 150px;
}

.content-container {
  display: flex;
  align-items: center;
}
.about-me-title {
  font-size: 18px;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: #333;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: #fff;
  padding: 16px 16px;
  border: 2px solid #dcd3d3;
  border-radius: 7px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

.my-image {
  max-width: 230px;
  margin-right: 20px;
  margin-bottom: 20px;
  border: 1px solid #a5a4a4;
  border-radius: 7px;
}

.text-container {
  flex: 1;
}

.info {
  p {
    font-size: 18px;
    line-height: 1.5;
    color: #333;
  }
  .cv-link {
    text-decoration: none;
    color: lightseagreen;
  }
  .cv-link:hover {
    margin-left: 5px;
  }
}

@media (max-width: 768px) {
  .about-me {
    margin-top: 50px;
  }

  .content-container {
    flex-direction: column;
    text-align: center;
  }

  .my-image {
    max-width: 150px;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .about-me-title {
    font-size: 20px;
    margin-bottom: 30px;
  }

  .info p {
    font-size: 16px;
    margin-bottom: 20px;
    text-align: justify;
  }
}
